<template>
    <div class="info_container">
        <head-top head-title="账户信息" goBack="true"></head-top>
        <section class="profile-info">
            <section class="headportrait">
                <input type="file" class="profileinfopanel-upload" @change="uploadAvatar">
                <h2>头像</h2>
                <div class="headportrait-div">
                    <img :src="imageBaseUrl+userInfo.avatar" v-if="userInfo" alt="" class="headportrait-div-top">
                    <span class="headportrait-div-top" v-else>
                        <svg>
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#avatar-default"></use>
                        </svg>
                    </span>
                    <span class="headportrait-div-bottom">
                        <svg fill="#d8d8d8">
                            <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                        </svg>
                    </span>
                </div>
            </section>
            <router-link to="/profile/setusername" class="info-router">
                <section class="headportrait headportraitwo">
                    <h2>用户名</h2>
                    <div class="headportrait-div">
                        <p>{{username}}</p>
                         <span class="headportrait-div-bottom">
                            <svg fill="#d8d8d8">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                            </svg>
                        </span>
                    </div>
                </section>
            </router-link>
            <router-link to="/profile/info/address" class="info-router">
                <section class="headportrait headportraitwo headportraithree">
                    <h2>收货地址</h2>
                    <div class="headportrait-div">
                         <span class="headportrait-div-bottom">
                            <svg fill="#d8d8d8">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                            </svg>
                        </span>
                    </div>
                </section>
            </router-link>
            <section class="bind-phone">
                账号绑定
            </section>
            <section @click="changePhone" class="info-router">
                <section class="headportrait headportraitwo headportraithree">
                    <h2><img src="../../../images/bindphone.png" style="display:inline-bock;margin-right:0.4rem">手机</h2>
                    <div class="headportrait-div">
                        <p>{{infotel}}</p>
                           <span class="headportrait-div-bottom">
                                <svg fill="#d8d8d8">
                                    <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                                </svg>
                            </span>
                    </div>
                </section>
            </section>
            <section class="bind-phone">
                安全设置
            </section>
            <section class="info-router">
                <router-link to="/forget" class="headportrait headportraitwo headportraithree">
                    <h2>登陆密码</h2>
                    <div class="headportrait-div">
                        <p>修改密码</p>
                        <span class="headportrait-div-bottom">
                            <svg fill="#d8d8d8">
                                <use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#arrow-right"></use>
                            </svg>
                        </span>
                    </div>
                </router-link>
            </section>
            <section @click="exitlogin" class="exitlogin">推出登陆</section>
            <section v-if="show" class="coverpart">
                <section class="cover-background"></section>
                <section class="cover-content" :class="{'cover-animate':isEneter,'cover-animat-leave':isLeave}">
                    <div class="sa-icon">
                        <span class="sa-body"></span>
                        <span class="sa-dot"></span>
                    </div>
                    <h2>是否退出登陆</h2>
                    <div class="sa-botton">
                        <button class="waiting" @click="waitingThing">在等等</button>
                        <div class="" style="display:inline-block">
                            <button class="quitlogin" @click="outLogin">退出登陆</button>
                        </div>
                    </div>
                </section>
            </section>
        </section>
        <transition mode="out-in" name="router-slid">
            <router-view></router-view>
        </transition>
        <alert-tip v-if="showAlert" @closeTip="showAlert = false" :alertText="alertText"></alert-tip>
    </div>
</template>

<script>
import {mapState,mapMutations} from 'vuex';
import headTop from '../../../components/header/header'
import alertTip from '../../../components/common/alertTip'
import {signout} from '../../../service/getData'
import {removeStore} from '../../../config/mUtils'
import {getImgPath} from '../../../components/common/mixin'
import {imageBaseUrl} from '../../../config/env'
export default {
    data()
    {
        return{
            imageBaseUrl,
            showAlert:false,
            alertText:null,
            username:'',//用户名
            resetname:'',//重置用户名
            infotel:'',//用户手机
            avatar:null,//用户头像
            isEneter:true,//是否登陆
            isLeave:false,//是否推出
            show:false,//显示提示框
        }
    },
    beforeDestroy () {
      clearInterval(this.timer);
    },
    mixins: [getImgPath]
    ,
    components: {
        headTop,
        alertTip
    },created () {
        
    },computed: {
        ...mapState([
            'userInfo','imgPath'
        ])
    },methods: {
        ...mapMutations([
            'OUT_LOGIN','SAVE_AVANDER'
        ]),
        //上传头像
        async uploadAvatar()
        {
              console.log(1);
            if(this.userInfo)
            {
                //querySelector() 方法返回文档中匹配指定 CSS 选择器的一个Dom元素。
                let input=document.querySelector('.profileinfopanel-upload');
                //FormData上传文件的对象
                let data=new FormData();
                data.append('file',input.files[0]);
                try{
                    let response=await fetch('http://cangdu.org:8001/eus/v1/users/' + this.userInfo.user_id + '/avatar',{
                        method:'POST',
                        //credentials配置cookie是否发送发送类型
                        //include cookie既可以同域发送，也可以跨域发送
                        credentials:'include',
                        body:data
                    },'POST');
                    //json()获取返回的数据
                    let res=response.json();
                    console.log(res);
                    if(res.status==1)
                    {
                        this.userInfo.avatar=res.image_path;
                    }
                }catch(error){
                    this.showAlert=true;
                    this.alertText='上传失败';
                    throw new Error(error);
                }
            }
        },
        changePhone()
        {
            this.showAlert=true;
            this.alertText="请在App环境下绑定";
        },
        async exitlogin(){
            this.show=true;
            this.isEneter=true;
            this.isLeave=true;
        },
        //取消推出
        waitingThing()
        {
            clearInterval(this.timer);
            this.isEneter=false;
            this.isLeave=false;
            this.timer=setInterval(()=>{
                clearInterval(this.timer);
                this.show=false;
            },200)
        },
        //退出登陆
        async outLogin()
        {
            this.OUT_LOGIN();
            this.waitingThing();
            this.$router.go(-1);
            removeStore('user_id');
            await signout();
        }
    },watch: {
        userInfo:function(value)
        {
            this.username=value.username;
            this.infotel=value.infotel;
            this.avatar=value.avatar;
        }
    }
}
</script>

<style lang="scss" scopde>
@import '../../../style/mixin.scss';
.info_container{
    position: absolute;
    top:0;
    bottom:0;
    right:0;
    left:0;
    background: #f5f5f5;
    z-index: 103;
    padding-top: 1.95em;
    p, span{
        font-family: Helvetica Neue,Tahoma,Arial;
    }
}
.profile-info{
    @include wh(100%,3.1rem);
    .profileinfopanel-upload{
        @include wh(100%,3.1rem);
        display: block;
        position: absolute;
        top: 2.35rem;
        left:0;
        opacity: 0;
    }
    .headportrait{
        margin-top: 0.4rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #fff;
        border:1px solid #ddd;
        padding:0.4rem 0.5rem;
        h2{
            display: flex;
            align-items: center;
            @include sc(.7rem,#333);
            font-weight: 500;
        }
        .headportrait-div{
            span{
                display: inline-block;
                svg{
                        @include wh(100%,100%);
                }
            }
            .headportrait-div-top{
                @include wh(2rem,2rem);
                @include borderRadius(50%);
                //图像位置紧贴父元素中部
                vertical-align:middle;
            }
            .headportrait-div-bottom{
                    @include wh(.66667rem,1.4rem);
                    position:relative;
                    top:0.44rem;
            }
        }
        
    }
    .headportraitwo{
        margin-top: 0;
        padding:.3rem .4rem;
        .headportrait-div{
            @include fj(left);
            p{
                text-align: left;
                @include sc(0.7rem,#999);
                line-height: 1.39rem;
                margin-right: .2rem;
                font-weight: 100;
                font-family: Arial;
            }
            .headportrait-div-bottom{
                    top:0;
            }
        }
    }
}
.info-router{
    display: block;
}
.bind-phone{
    padding:.4rem .4rem;
    @include sc(0.5rem,#666);
}
.headportraithree{
    border-bottom: 1px solid #ddd;
}
.exitlogin{
    
    border:0.25rem solid #d8584a;
    background: #d8584a;
    margin: 0 0.5rem;
    margin-top: 0.9rem;
    border-radius: 0.2rem;
    text-align: center;
    @include sc(0.7rem,#fff);
}
.coverpart{
    @include wh(100%,100%);
    @include allover;
    .cover-background{
        @include wh(100%,100%);
         @include allover;
        background: #000;
        z-index: 100;
        opacity: .2;
    }
    .cover-content{
        width: 94%;
        background: $fc;
        padding:17px;
        position: absolute;
        top:20%;
        left:3%;
        z-index: 1000;
        .sa-icon{
            @include wh(90px,90px);
            border:4px solid #f8bb86;
            @include borderRadius(50%);
            margin:20px auto;
            position: relative;
            .sa-body{
                @include wh(5px,47px);
                @include borderRadius(2px);
                @include cl;
                background:#f8bb86;
                top: 10px;
            }
            .sa-dot{
                @include wh(7px,7px);
                @include borderRadius(50%);
                @include cl;
                background:#f8bb86;
                bottom: 10px;
            }
        }
        h2{
            width: 100%;
            text-align: center;
            @include sc(30px,#575757);
            margin:25px 0;
            font-weight: 500;
        }
        .sa-botton{
            width: 100%;
            text-align: center;
            button{
                display: inline-block;
                padding:.4rem 1rem;
                 @include borderRadius(5px);
                 @include sc(.6rem,$fc);
                 //letter-spacing 属性增加或减少字符间的空白（字符间距）。
                  letter-spacing:1px;
                  margin-top: 26px;
            }
            .waiting{
                background: #c1c1c1;
               margin-right: .4rem;
            }
            .quitlogin{
                background:rgb(221, 107, 85);
            }
        }
    }
}
@-webkit-keyframes bounceIn{
    from,20%,40%,60%,80%,100%{
        //animation-timing-function 规定动画的速度曲线 
        // 使用名为三次贝塞尔（Cubic Bezier）函数的数学函数，来生成速度曲线。您能够在该函数中使用自己的值，也可以预定义的值：
        // cubic-bezier 函数中自己的值。可能的值是从 0 到 1 的数值。
        -webkit-animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
        animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
    }
    0%{
        opacity: 0;
        //scale3d 3d缩放
        -webkit-transform: scale3d(.3, .3, .3);
        transform: scale3d(.3, .3, .3);
    }
    20%{
         -webkit-transform: scale3d(1.1, 1.1, 1.1);
        transform: scale3d(1.1, 1.1, 1.1);
    }
    40%{
        -webkit-transform: scale3d(.9, .9, .9);
        transform: scale3d(.9, .9, .9);
    }
    60%{
            opacity: 1;
    -webkit-transform: scale3d(1.03, 1.03, 1.03);
    transform: scale3d(1.03, 1.03, 1.03);
    }
    80%{
        -webkit-transform: scale3d(.97, .97, .97);
        transform: scale3d(.97, .97, .97);
    }
    100%{
            opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
    }
}
@keyframes bounceIn {
  from, 20%, 40%, 60%, 80%, 100% {
    -webkit-animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
    animation-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
  }

  0% {
    opacity: 0;
    -webkit-transform: scale3d(.3, .3, .3);
    transform: scale3d(.3, .3, .3);
  }

  20% {
    -webkit-transform: scale3d(1.1, 1.1, 1.1);
    transform: scale3d(1.1, 1.1, 1.1);
  }

  40% {
    -webkit-transform: scale3d(.9, .9, .9);
    transform: scale3d(.9, .9, .9);
  }

  60% {
    opacity: 1;
    -webkit-transform: scale3d(1.03, 1.03, 1.03);
    transform: scale3d(1.03, 1.03, 1.03);
  }

  80% {
    -webkit-transform: scale3d(.97, .97, .97);
    transform: scale3d(.97, .97, .97);
  }

  100% {
    opacity: 1;
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
@keyframes zoomOut {
    //from to等价于0% 100%
    from{
        opacity: 1;
    }
    50%{
        opacity: 0;
        -webkit-transform: scale3d(.3, .3, .3);
        transform: scale3d(.3, .3, .3);
    }
    100%{
        opacity: 0;
    }
}
body .coverpart .cover-animate{
    transition: all 1s;
    animation: bounceIn .6s;
}
body .coverpart .cover-animate-leave{
    animation: zoomOut .4s;
}
.router-slid-enter-active, .router-slid-leave-active {
    transition: all .4s;
}
.router-slid-enter, .router-slid-leave-active {
    transform: translate3d(2rem, 0, 0);
    opacity: 0;
}
</style>



